body{
  padding: 5px;
  margin: 5px;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p {
padding: 5px;
text-align: center;
}

.card img {
  max-height: 170px;
}
